.btn-start {
  outline: none;
  border: none;
  padding: 10px;
  background-color: rgb(10, 180, 83);
  color: black;
  font-size: 24px;
  font-weight: 600;
  width: 120px;
  margin: 10px;
  text-align: center;
  border-radius: 12px;
  transition: all 0.3s ease;
}

.btn-start:hover {
  transform: scale(0.9);
  box-shadow: 0 0 1px 4px rgb(87, 85, 85);
  cursor: pointer;
}

